import logo from '../logo.svg'
import {useContext} from '../provider/Provider'
import {NavLink} from 'react-router-dom'
import { useState } from 'react'

export default function Header({isLogin}) {
    const {modal,setModal} = useContext();
    const handelModal = () => {
        setModal(!modal)
    }
    return (
        <header className='container'>
            <nav className='navbar'>
                <div className='logo'><img src={logo} alt="this is web logo" /></div>
                <ul className='tab-menu'>
                    <li className='item-tab'><NavLink to="/" exact={true}>خانه</NavLink></li>
                    <li className='item-tab'><NavLink to="/product">محصولات</NavLink></li>
                    <li className='item-tab'><NavLink to="/cyber">اخرین خبر ها</NavLink></li>
                    <li className='item-tab'><NavLink to="/whyreact">چرا ریکت؟</NavLink></li>
                    <li className='item-tab'><NavLink to="/about">درباره سایت</NavLink></li>
                    <li className='item-tab'><NavLink to="/contact">تماس با ما</NavLink></li>
                </ul>
                <NavLink to='/dashbord' className='login_sing-up' style={{display: isLogin ? 'block' : 'none'}}>
                    <div>داشبورد</div>
                </NavLink>
                <button className='login_sing-up' style={{marginRight:isLogin ? 10 : 'auto'}} onClick={handelModal} >
                    <div>ثبت نام و ورود</div>
                </button>
            </nav>
        </header>
    )

}