import { NavLink } from "react-router-dom";
import React, { useEffect, useState, useRef } from "react";
import axios from "axios";
import { initstateContextApi} from "../../provider/Provider";
function Product({ props }) {
  const productRef = React.useRef();
  const [state, setState] = useState({ prodect: [] });
  const listElement = useRef(null);
  const {initstate} = initstateContextApi();
  document.title ='صفحه محصولات'
  return (
    <div>
      <h4>محصولات</h4>
      <NavLink to="/product/creat">ساخت پست</NavLink>
      <br />
      <NavLink to="/product/singel">تکی</NavLink>
      <section className="prodects" ref={listElement}>
        {initstate.product.map((item, i) => {
          return (
            <NavLink key={item.id} to={`product/${item.id}`} className="posted" ref={productRef}>
              <img src={item.url} alt="" />
              <h4 className="title">{item.title}</h4>
              <strong className="price">{item.price}</strong>
            </NavLink>
          );
        })}
      </section>
    </div>
  );
}
export default Product;
