import "./App.css";
import React, { useEffect, useState } from "react";
import Header from "./component/Header";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import WhyReact from "./component/whyReactpage/WhyReact";
import About from "./component/About/About";
import Contact from "./component/Contact/Contact";
import Home from "./component/Home/Home";
import { useContext, initstateContextApi } from "./provider/Provider";
import Footer from "./component/Home/Footer";
import Product from "./component/Product/Product";
import CreatProduct from "./component/Product/CreatProduct";
import Singelproduct from "./component/Product/SingelProduct";
import Cyber from "./component/News/News";
import Dashbord from './component/dashbord/Dashbord'

function App() {
  const [form_modal, setForm_modal] = useState(false);
  const [both, setBoth] = useState(false);
  const [log , setLog ]  = useState({num:'',pass:''})
  const modalRef = React.useRef();
  const { modal, setModal } = useContext();
  const { initstate } = initstateContextApi();
  const openModal = () => {
    if (modal) {
      return {
        top: "25%",
        transform: "scale(1)",
        opacity: 1,
        left: moveModal(form_modal ? "-50%" : "25%"),
      };
    } else {
      return {
        top: "20%",
        transform: "scale(0)",
        opacity: 0,
        left: moveModal(form_modal ? "-50%" : "25%"),
      };
    }
  };
  const openModal2 = () => {
    if (modal) {
      return {
        top: "25%",
        transform: "scale(1)",
        opacity: 1,
        left: moveModal(form_modal ? "25%" : "100%"),
      };
    } else {
      return {
        top: "20%",
        transform: "scale(0)",
        opacity: 0,
        left: moveModal(form_modal ? "25%" : "100%"),
      };
    }
  };
  const btnModal = () => {
    if (modal) {
      return {
        transform: "scale(1)",
        opacity: 1,
        right: moveBtn(form_modal ? "92%" : "1%"),
      };
    } else {
      return {
        transform: "scale(0)",
        opacity: 0,
        right: moveBtn(form_modal ? "92%" : "1%"),
      };
    }
  };
  const moveModal = (n) => {
    return n;
  };
  function moveBtn(n) {
    return n;
  }
  const openModalbackdroup = () => {
    if (modal) {
      return "block";
    } else {
      return "none";
    }
  };

  return (
    <div>
      <BrowserRouter>
        <Header isLogin={both} />
        <Switch>
          <div className="container">
            <Route exact path="/" component={Home} />
            <Route path="/whyreact" component={WhyReact} title="چرا ریکت؟" />
            <Route path="/about" component={About} />
            <Route path="/contact" component={Contact} />
            <Route
              path="/product"
              exact
              render={(props) => <Product {...props} />}
            />
            <Route
              path="/product/:id"
              exact
              render={(props) => <Singelproduct {...props} {...initstate} />}
            />
            <Route path="/product/creat" component={CreatProduct} />
            <Route path="/cyber" component={Cyber} />
            <Route path="/dashbord" render={(props)=> <Dashbord {...props} isLogin={both} />}/>
          </div>
        </Switch>
        <Footer />
      </BrowserRouter>
      <div
        className="blur-backdrop"
        onClick={() => setModal(false)}
        style={{ display: openModalbackdroup() }}
      ></div>
      <div className="modal" ref={modalRef} style={openModal()}>
        <form
          className="container p-4"
          onSubmit={(e) => {
            e.preventDefault();
            let loginData = {
              name:e.target.elements[0].value,
              number:e.target.elements[1].value,
              password:e.target.elements[2].value
            }
            localStorage.setItem("dataLogin",JSON.stringify(loginData))
          }}
        >
          <div>
            <input type="text" placeholder="نام خود را وارد کنید" />
          </div>
          <div>
            <input type="number" placeholder="شماره همراه خود را وارد کنید" />
          </div>
          <div>
            <input type="password" placeholder="رمز عبور " />
          </div>
          <button type="submit">ثبت نام</button>
        </form>
      </div>
      <div className="modal2" style={openModal2()}>
        <form className="container p-4" onSubmit={(e)=>{
          e.preventDefault();
          let num = e.target.elements[0].value
          let pass = e.target.elements[1].value
          setLog({
            num,
            pass
          })
          if(JSON.parse(localStorage.getItem("dataLogin")).number == num && JSON.parse(localStorage.getItem('dataLogin')).password == pass ){
            setBoth(true)
          }
        }}>
          <div>
            <input type="number" placeholder="شماره همراه خود را وارد کنید" />
          </div>
          <div>
            <input type="text" placeholder="رمز عبور " />
          </div>
          <button type='submit'>ورود شدن</button>
        </form>
      </div>
      <div
        className="btn-next-login-or-reg"
        style={btnModal()}
        onClick={(e) => {
          setForm_modal(!form_modal);
          e.target.textContent = form_modal ? "ورود" : "ثبت نام";
        }}
      >
        ورود
      </div>
    </div>
  );
}

export default App;
