
export default function dashbord({isLogin}){
    function isLoginDashbord() {
        if(isLogin){
            return (
                <h3>وارد شدید</h3>
            )
        }else {
            return (
                <h3>وارد نشدید</h3>
            )
        }
    }
    return (
        <>
          {isLoginDashbord()}
          <h4>داشبورد</h4>
        </>
    )
}