import react, { useState } from "react";
import axios from "axios";

const CountContext = react.createContext();
const initstateContext = react.createContext();
const News = react.createContext();

function CountProvider({ children }) {
  const [modal, setModal] = useState(false);
  const [initstate, setInitState] = useState({ product: [] });
  const [news, setNews] = useState({News:[]});
  


  react.useEffect(() => {
    axios
      .get("http://127.0.0.1:5500/api.json")
      .then((respons) => {
        let data = respons.data.slice(0, 10);
        setInitState({ product: data });
      })
      .catch((err) => {
        alert(err);
      });
    // axios
    //   .get('https://developers.parsijoo.ir/web-service/v1/price/?q=ریال&type=search',{
    //     headers:{
    //       'api-key':"aae995f928594d098cdaa04a3df52f7c"
    //     }
    //   })  
    //   .then((respons)=>{
    //     setNews({News:respons.data.result.items})
    //   })
    //   .catch(err=>{
    //     console.log(err)
    //   })
      async function getNews(){
        let response = await fetch('https://developers.parsijoo.ir/web-service/v1/price/?q=ریال&type=search',{
          headers:{
            'api-key':"aae995f928594d098cdaa04a3df52f7c"
          }
        }) 
        let data = await response.json()
        setNews({News:data})
      }
      getNews()

  }, []);

  return (
    <News.Provider value={news}>
      <initstateContext.Provider value={{ initstate, setInitState }}>
        <CountContext.Provider value={{ modal, setModal }}>
          {children}
        </CountContext.Provider>
      </initstateContext.Provider>
    </News.Provider>
  );
}
export function useContext() {
  return react.useContext(CountContext);
}
export function initstateContextApi() {
  return react.useContext(initstateContext);
}
export function newsContext() {
  return react.useContext(News);
}
export default CountProvider;
