export default function Contact() {
  document.title ='ارتباط با ما'
  return (
    <div className="container dd">
      <form action="" className='Contact-Form'>
        <label htmlFor="name">نام:</label>
        <br />
        <input type="text" id="name" placeholder="نام خود را وارد کنید" />
        <br />
        <label htmlFor="email">ایمیل:</label>
        <br />
        <input type="text" id="email" placeholder="ایمیل خود را وارد کنید" />
        <br />
        <label htmlFor="text">توضیحات:</label>
        <br />
        <textarea name="" id="text" cols="30" rows="10" placeholder="توضیح دهید..."></textarea> 
        <br />
        <button>ارسال</button>
      </form>
    </div>
  );
}
