export default function SingelProdect(props) {
  if (props.product.length == 0) {
    return false;
  }
  let prdct = props.product.find((item)=> item.id == props.match.params.id);
  document.title =prdct.title
  return (
    <div>
      <section className="singelProduct">
        <div className='right'>
          <figcaption className="caption">{prdct.title}</figcaption>
          <img
            src={prdct.url}
            alt="this tis imge product"
          />
          <strong className="pricesingel">{prdct.price}</strong>
        </div>
        <div className='left'></div>
      </section>
    </div>
  );
}
