import { useEffect } from 'react'
import img1 from '../../img/1.png'
import code from '../../img/img1.png'
import Footer from './Footer'
import Card from './section/Card'
import HeaderCard from './section/HeaderCard'

export default function Home() {
    document.title ='خانه'
    return (
        <div className='container'>
            <HeaderCard img={img1} dir={'rtl'} id='sec1' />
            <Card img={code} dir={'ltr'} id='sec2' />
            <Card img={code} dir={'rtl'} id='sec3' />
        </div>
    )
}